<template>
  <div class="Login-box">
    <div class="Login-img">
      <img
        src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019pILfAg7Avr1567732916.png"
        alt=""
      />
    </div>

    <div class="lwh_input" v-for="(item, index) in lwh_value" :key="index">
      <input
        :class="{ lwh_valueIndex: index == lwh_valueIndex }"
        :type="item.txt"
        v-model="item.cont"
        :placeholder="item.place"
        @focus="lwh_focus(index)"
        @blur="lwh_blur()"
      />
    </div>
    <div class="vnt-text">
      <span @click="add1">找回密码</span>
      <span @click="add"> 注册/验证码登录</span>
    </div>

    <div style="margin: 16px">
      <van-button
        round
        block
        type="info"
        native-type="submit"
        color="linear-gradient(to right, rgb(255, 146, 71),rgb(253, 88, 3))"
        @click="lwh_btn()"
      >
        登录
      </van-button>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      lwh_value: [
        { cont: "", txt: "text", type: "name", place: "请输入手机号" },
        { cont: "", txt: "password", type: "pass", place: "请输入密码" },
      ],
      lwh_valueIndex: null,
    };
  },
  methods: {
    onSubmit(values) {
      console.log("submit", values);
    },
    add() {
      this.$router.push({
        path: "/wyl-sms",
      });
    },
    add1() {
      this.$router.push({
        path: "/wyl-pass",
      });
    },
    // 获取焦点
    lwh_focus(index) {
      this.lwh_valueIndex = index;
    },
    // 失去焦点
    lwh_blur() {
      this.lwh_valueIndex = null;
    },
    // 登录
    async lwh_btn() {
      console.log(this.lwh_value[0].cont, this.lwh_value[1].cont);
      let { data } = await this.$http.post(
        "http://120.53.31.103:84/api/app/login",
        {
          mobile: this.lwh_value[0].cont,
          password: this.lwh_value[1].cont,
          type: 1,
        }
      );
      if (data.code != 200) return Toast(data.msg);
      window.sessionStorage.setItem("token", data.data.remember_token);
      Toast(data.msg);
      this.$router.push({
        path: "/lwh_my",
        query: "nickname",
      });
    },
  },
};
</script>

<style scoped lang="scss">
.Login-box {
  width: 100vw;
  height: 100vh;
}
.Login-img {
  width: 100%;
  height: 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Login-img img {
  width: 2.3rem;
  height: 0.6rem;
}

.vnt-text {
  width: 80vw;
  font-size: 15px;
  color: gray;
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin-left: 0.4rem;
  margin-top: 0.3rem;
}
.van-button {
  width: 70vw;
  margin-top: 0.6rem;
  margin-left: 0.4rem;
}
.van-field {
  width: 85vw;
  text-align: center;
  margin-left: 0.3rem;
}
.lwh_input {
  width: 3.2rem;
  height: 0.52rem;
  margin-left: 0.41rem;
  margin-top: 0.02rem;
  > input {
    width: 100%;
    border: none;
    border-bottom: 0.01rem solid #f1f1f1;
    height: 100%;
  }
  > .lwh_valueIndex {
    border-bottom: 0.01rem solid #eb6100;
  }
  > input::placeholder {
    color: #b7b7b7;
  }
}
</style>